<<template>
    <div>
        <van-nav-bar
            title=""
           />
        <van-cell-group>
		   <van-field
		    v-model="phone"
		    label=""
		    placeholder=""
		    error-message=""
		  />
		  <van-field
		    v-model="password"
		    label=""
		    placeholder=""
		    error-message
		  />
		</van-cell-group>
		
		<van-button type="primary" size="large" @click="tap"></van-button>
		
    </div>
</template>

<script>
import axios from 'axios'
export default {
	data(){
		return{
			phone:"",
			password:""
		}
	},
	methods:{
		tap(){
			
			axios({
				method:"post",
				url:"http://106.12.52.107:8081/MeledMall/user/register",
				params:{phonenum:this.phone,password:this.password}
			}).then((data)=>{
				//console.log(data.data)
				if(data.data.code == 1){
						const toast = this.$toast.loading({
						duration: 0,       //  toast
						forbidClick: true, // 
						loadingType: 'spinner',
						message: ',...'
						});
						let second = 3;
						const timer = setInterval(() => {
						second--;
						if (second) {
							toast.message = `,...`;
						} else {
							clearInterval(timer);
							this.$toast.clear();
							this.$router.push("/jssLogin")
							
						}
						}, 1000);
				}else{
					this.$dialog.alert({
						title: '',
						message: ''
						}).then(() => {
						// on close
					});
				}
			
			})
		},
		
	}
}
</script>

<style>

</style>



