<template>
    <div>
         <van-nav-bar
            title=""
            left-text=""
            :fixed= 'true'
            left-arrow
            @click-left="onClickLeft"
            
        />
        <div id="cartpay">
        <div class="cartpay" >
        <van-card
            v-for="(item,index) in list"
           
            :price="item.mprice"
            desc=""  
            :title="item.mname"
            :thumb="item.mphoto" 
            tag=''
            :key='item'
           
        >
            
            
            <div slot="footer" class="checkbox">
            <van-stepper v-model="item.mnum" :disable-input='true' @change='change(index,item.danxuan,item.mnum)' :step='step'/>
               <van-checkbox   v-model="item.danxuan" class="checkedBox" @click='tao(item.danxuan,index)'></van-checkbox>
            </div>
        </van-card>
        
       </div>
            <van-submit-bar
            :price="sum"
            button-text=""
            @submit="onSubmit()"
            >
            <van-checkbox v-model="checked" @click='chioceall'></van-checkbox>
            <span slot="tip">
                <van-notice-bar
                text=""
                left-icon="volume-o"
                />

            </span>
            </van-submit-bar>

        </div>
      
    </div>
</template>

<script>
import axios from 'axios'
import {mapState,mapGetters,mapActions} from 'vuex';

export default {
        data(){
            return{
                list:[],
                checked:false,
                isLoading: false,
                value:1,
                step:1,
                djiage:'',
                sum : 0,
                pri:[],
                xuan:0,
                list1:[],
                money:0,
                itemNum:[],
                mid:[],
                timer:[],
                id:null
                
            }
        },
        methods: {
        	
            onClickButton(){
                this.$router.push('/addMessage')
            },
            onClickLeft(){
                this.$router.go(-1)
            },
            onClickIcon(){

            },
            onSubmit(){
               var _this = this
                if(this.sum == 0){
                    //alert('')
                    this.$toast('');
                }else if(this.sum/100 > this.money){
                     this.$toast('')
                }else{
                    const toast =this.$toast.loading({
                    duration: 0,       //  toast
                    forbidClick: true, // 
                    loadingType: 'spinner',
                    message: '...'
                    });
                    let second = 3;
                    const timer = setInterval(() => {
                    second--;
                    if (second) {
                        this.$toast.message = `...`;
                    } else {
                        clearInterval(timer);
                        this.$toast.clear();
                        this.$router.push('/addMessage')

                    }
                    }, 1000);
                    this.list.forEach((item,index)=>{

                        if(item.danxuan){
                            this.xuan++
                            //this.num = this.xuan.length
                            this.itemNum.push(this.list[index].mnum)
                            this.mid.push(this.list[index].mid)
                            //console.log(this.list[index].mid,this.list[index].mnum)
                            //
                            this.getDate()
                            //this.timer.push(this.timer)
                        }
                       
                    })
                  this.$store.commit('onSubmit',this.xuan)//
                  this.$store.commit('huafei',this.sum)//
                  this.$store.commit('itemNum',this.itemNum)//mutationsitemNum
                   this.$store.commit('mid',this.mid)//mutations  mid
                    this.$store.commit('timer',this.timer)//mutations  mid
                }
            },
            chioceall(){
                this.sum = 0*0
                if(!this.checked){
                    for(let val of this.list){
                     val.danxuan = true;
                    //  this.sum += val.price * val.bujin*100
                    this.jine()
                    }
                }else{
                    for(let val of this.list){  
                     val.danxuan = false;
                     this.sum = 0*0
                    }
                }
                
            },
            getDate(){
           var date = new Date();
            var year = date.getFullYear()
            var month = date.getMonth()+1
            var day = date.getDate()
            var min = date.getMinutes()
             var hour = date.getHours()
             var min = date.getMinutes()
             var ms = date.getSeconds()
            var time = year+'.'+ month+'.'+ day+' '+hour+':'+min+':'+ms
               this.timer.push(time)
            },
            tao(danxuan,index,a){
                let p =parseFloat(this.list[index].mprice)*parseFloat(this.list[index].mnum).toFixed(2)
                if(!danxuan){
                   
                    this.sum +=p*100

                }else{
                    this.sum -=p*100
                    if(this.sum == 0){
                        this.checked = false
                    }
                }
                
            },
            change(index,danxuan,bujin){
                  //let num = bujin+1;
                  this.jine()//list
            },
            jine(){
                this.sum = 0*0 //
                this.list.forEach((item,index) => {
                    if(item.danxuan){//
                        this.sum += item.mprice*item.mnum*100 //
                    }
                });
                
            }
        },
        mounted() {
        	var id = localStorage.getItem('id')
        	this.id = id
            axios({
                method:'post',
                url:'http://106.12.52.107:8081/MeledMall/shopCar/showShopCar',
                params:{uid:this.id}
                // data:{token:token}
            }).then((data)=>{
                //console.log(data.data.info)
                this.list =data.data.info
                
            }),
             axios({
                method:'post',
                url:'http://106.12.52.107:8081/MeledMall/user/mine',
                params:{id:this.id}
            }).then((data)=>{
                //console.log(data.data.info)
                this.money = data.data.info.user.balance
            })
            
        },
}   
</script>
<style scoped="">
.cartpay{
    margin:46px 0 120px 0;
}
.check{
    position: absolute;
    left: 30px;
    bottom:0;
}

input[type='checkbox']{
    width: 16px;
    height: 16px;
    background-color: #fff;
    -webkit-appearance:none;
    border: 1px solid #c9c9c9;
    border-radius: 2px;
    outline: none;
}
 
.checkbox input[type=checkbox]:checked{
   background: url("https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=964899143,2534335338&fm=15&gp=0.jpg") 0 0/16px 16px no-repeat;

}
</style>


