var mutations = {
	huoqu(state,id){
		state.id=id
	},
	onSubmit(state,num){
        state.num = num
    },
    huafei(state,huafei){
        state.huafei = huafei
    },
    itemNum(state,itemNum){
        state.itemNum =state.itemNum.concat(itemNum)//
    },
    mid(state,mid){
        state.mid = state.mid.concat(mid)//Mid
    },
    timer(state,timer){
        state.timer = state.timer.concat(timer)
    },
    delMid(state,delMid){
        state.delMid = state.delMid.concat(delMid)
    },
    fileContent(state,fileContent){
        state.fileContent = fileContent
    }
}

export default mutations;






