<template>
    <div>
            <van-nav-bar
            title=""
            left-text=""
            left-arrow
            @click-left="onClickLeft"
           />
           <van-cell-group>
           
            <van-cell title="" value=" > " label="" :center='true' to='/setPassword'/>
            </van-cell-group>
            <van-cell title="" value=" > " label="" :center='true' />
            </van-cell-group>
            <van-cell title="" value="  > " label="30k" :center='true' />
            </van-cell-group>
            <van-cell title="" value="1.0.1" label="" :center='true' />
            </van-cell-group>
            <div id='tuichu'>
            <van-button type="primary" size="normal" :block='true' :id="out" @click='out1()'></van-button>
            </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            out:'out'
        }
    },
    methods: {
    onClickLeft() {
        this.$router.go(-1)
    },
    out1(){
    	
    	this.$dialog.confirm({
		  title: '',
		  message: ''
		}).then(() => {
		  // on confirm
		  localStorage.removeItem('id')
      this.$store.commit('fileContent','https://img.yzcdn.cn/vant/cat.jpeg')
      this.$store.commit('onSubmit',0)
      
       this.$router.push('/jssHome');
		  
		}).catch(() => {
		  // on cancel
		   
		});
    }
  }
}
</script>

<style scoped="">
#tuichu{
    width: 100%;
    display: flex;
    justify-content: center;
}
#out{
    width: 70%;
    margin-top: 50px
}
</style>



