<template>
    <div>
        <van-nav-bar
            title=""
            left-text=""
            
            left-arrow
            @click-left="onClickLeft"
         
        />
    <van-panel title="" desc="" status="">
        
        </van-panel>
        <van-cell-group>
        <van-field v-model="value" placeholder="" />
        </van-cell-group>
        <div class="pay">
        <van-button type="primary" @click="pay()"></van-button>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    data(){
        return{
            value:'',
            id:null,
            
           
        }
    },
     methods: {
            onClickLeft() {
                this.$router.go(-1)
            },
            pay(){
                axios({
                    method:'post',
                    url:'http://106.12.52.107:8081/MeledMall/user/recharge',
                    params:{balance:this.value,id:this.id}
                }).then((data)=>{
                    //console.log(data.data)
                    if(this.id){
                    this.$dialog.alert({
                        message: ''+this.value
                        })
                    }else{
                    	this.$dialog.alert({
                        message: ''
                        })
                    	
                    }
                })
            }
           
    },
    mounted(){
    	 var id = localStorage.getItem('id')
    	 this.id = id
    	
    }
}
</script>
<style scoped="">
    .pay{
        display: flex;
        justify-content: flex-end
    }
</style>


