<template>
	<div>
		<van-nav-bar
		  title=""
		/>
		<van-cell-group>
		   <van-field
		    v-model="phone"
		    label=""
		    placeholder=""
		    error-message
		  />
		  <van-field
		    v-model="password"
		    label=""
		    placeholder=""
		    error-message
		  />
		</van-cell-group>
		<div id="anniu">
			<van-button type="primary" @click="deng"></van-button>
			<van-button type="info" to="/jssRegister"></van-button>
		</div>
		
	</div>
</template>

<script>
	import axios from "axios"
	import {mapState,mapGetters,mapActions} from 'vuex';
	export default{
		data(){
			return{
				phone:"",
				password:"",
				id:0
			}
		},
		methods:{
			
			deng(){
				axios({
					method:"post",
					url:"http://106.12.52.107:8081/MeledMall/user/login",
					params:{phonenum:this.phone,password:this.password}
				}).then((data)=>{
					//console.log(data.data)
					this.id = data.data.info.id
					if(data.data.code == 1){
						 const toast = this.$toast.loading({
						duration: 0,       //  toast
						forbidClick: true, // 
						loadingType: 'spinner',
						message: ',...'
						});
						let second = 3;
						const timer = setInterval(() => {
						second--;
						if (second) {
							toast.message = `,...`;
						} else {
							clearInterval(timer);
							this.$toast.clear();
							this.$router.push("/jssHome")
							this.$store.commit("huoqu",this.id)
							window.localStorage.setItem("id",this.id)
						}
						}, 1000);
							
					}else{
						this.$dialog.alert({
						title: '',
						message: ''
						}).then(() => {
						// on close
						});
					}
				
				})
			}
		}
	}
</script>

<style scoped="">
	#anniu{
		margin-left:100px;
		margin-top: 40px;
	}
</style>


