<<template>
    <div>
        <van-nav-bar
            title=""
            left-text=""
          
            left-arrow
            @click-left="onClickLeft"
           
            />
        <van-cell-group>
        <van-cell title="" value="" label="" />
        </van-cell-group>
        <van-cell-group>
  <van-field
    v-model="phone"
    label=""
    placeholder=""
    :required='true'
    :yan='error'
    @blur='yz'	
    :error-message='error'
  />
  <van-field
    v-model="password"
    label=""
    placeholder=""
    :required='true'
    
  />
</van-cell-group>
<div class='psd'>
<van-button square type="primary"  size='large' :style='{marginTop:"25px",padding:"0 25px"}' @click='que()'></van-button>
</div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            error:'',
            phone:'',
            password:'',
            res:/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/,
        }
    },
    methods: {
         
        yz(){
            if(!this.res.test(this.phone)){
                this.error = ''
            }else{
                this.error = ''
            }
        },
        onClickLeft(){
            this.$router.go(-1)
        },
        que(){
            this.$notify('');
        }
    },
}
</script>

<style scoped="">
.psd{
    display: flex;
    justify-content:flex-end;

}
</style>


