<template>
    <div>
        <van-nav-bar
            title=""
            left-text=""
           
            left-arrow
            @click-left="onClickLeft"
            
        />
        <div class="addmessage">
        <van-address-edit
            :area-list="areaList"
            show-postal
            show-delete
            show-set-default
            show-search-result
            :search-result="searchResult"
            @save="onSave"
            @delete="onDelete"
            @change-detail="onChangeDetail"
        />
       </div>
    </div>
</template>


<script>
import areaList from '../assets/js/area.js'
export default {
   
    data(){
        return{
             areaList,
             searchResult: []
        }
    },
    methods: {
        onSave(){
            const toast = this.$toast.loading({
            duration: 0,       //  toast
            forbidClick: true, // 
            loadingType: 'spinner',
            message: ',...'
            });
            let second = 3;
            const timer = setInterval(() => {
            second--;
            if (second) {
                toast.message = `,...`;
            } else {
                clearInterval(timer);
                this.$toast.clear();
                this.$router.push('/my')
            }
            }, 1000);
        },
        onClickLeft(){
            this.$router.go(-1)
        },
        onChangeDetail(){

        },
        onDelete(){
            
        }
    },  
}
</script>


<style scope="">
.addmessage{
    margin-top:0px; 
}
</style>



